
.header{
  z-index: 99;
  width: 100%;
  height: 2.75rem;
  background: #fff;
  position: fixed;
  top: 0;
  margin-bottom: 0.5rem;
}
.tab{
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.tab .tab-item{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 44px;
}
.tab .center{
  text-align: center;
}
.one-icon-menu:before{
  content: "\e65b";
}
.one-icon-search:before{
  content: "\e645";
}
.search {
  float: right;
  font-size: 1.375rem;
  padding: 0 0.625rem;
}



