
.ui-content{
    border-width: 0;
    overflow: visible;
    overflow-x: hidden;
}
.logo-img {max-width: 3.75rem;
}
.download{padding-top: 1.875rem;width: 100%;height: 11.875rem;text-align: center;
}
/*以上为公共样式*/
.about{padding-bottom: 2.5rem;
}
.about .download{padding-top: 4.9375rem;height: 9.3125rem;
}
.mobile-website{color:#000;font-size: 0.875rem;font-weight: bolder;margin-top: 0.9375rem;margin: 0 0 1.25rem 0;
    line-height: 2.0rem;
}
.mobile-website strong{color: #000;
}
.about-content {
    font-size: 0.875rem;
    line-height: 1.625rem;
    font-weight: lighter;
    color: #000000;
    margin: 0 2.0625rem;
}
.copyright {
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 0;
}
.copyright p{font-size: 0.8125rem;line-height: 1.25rem;color: rgba(0, 0, 0, 0.3);margin: 0.6875rem 0 1.25rem;
}



