
.content-list-wrap{background: #f6f6f6;font-size: 1em;
}
.content-list{margin-top: 3.25rem;background: #f6f6f6;overflow: hidden;
}
.content-list p {
    margin: 0 1.25rem;
}
.content-list .text-tag{color: rgba(0, 0, 0, 0.6);line-height: 1.85em;font-size: 0.875em;text-align: center;padding: 0.7143em 0 1.0714em 0;
}
.content-list .text-title {
    font-size: 1.375em;
    line-height: 1.4em;
    max-height: 2.8em;
    margin-top: 0;
}
.ellipsis-row1, .ellipsis-row2 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}
.ellipsis-row2 {
    -webkit-line-clamp: 2;
}
.content-list .text-author {
  font-size: 1em;
  line-height: 1.875em;
  color: rgba(0,0,0,0.6);
  margin-top: 0.3125em;
  padding: 0 2.5em 0 0;
}
.content-list .text-cover-img {
  margin: 0.625em 1.25em;
  width: 20.9375em;
  height: 12.5em;
  background-image: url(../../../static/image/list-paceholder.png);
  background-size: cover;
  overflow: hidden;
}
.text-cover-img{position: relative;
}
.content-list .text-cover-img img {
    position: relative;
    margin: 0!important;
}
.content-list .img-center-x{height: 100%; width: auto;max-width: none;left:50%;-webkit-transform: translateX(50%);
}
.content-list .img-center-y {
    width: 100%;
    height: auto;
    max-height: none;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
}
.text-content-short {font-size: 0.875em;line-height: 1.875em;max-height: 3.714em;color: rgba(0, 0, 0, 0.6);margin-top: 0.7143em;
}
.content-list .text-date {
  font-size: 0.625em;
  line-height: 1.4em;
  padding: 1.8em 0 2.5em 0;
  color: #808080;
  text-align: left;
}



